import React from 'react';


const Material = props => (
    <div className="Material">
        <div className="MaterialType">{props.type}</div>
        <p class="tooltip">{props.title.length>25?props.title.slice(0,25) + '...':props.title}
            <span class="tooltiptext">{props.title}</span>
        </p>      
        <a download href={props.url} className="MaterialButton">Download</a>
    </div>
)

export default Material;