import React from 'react';
import './VideoPlayer.css';


const VideoPlayer = props => {
    
    return (
        <div className="VideoPlayerBackdrop" onClick={() => props.close()} >
            <video  onEnded={props.onEnded} autoPlay src={props.url} onClick={e => {
                e.stopPropagation();
            }} webkitplaysinline="true" controls preload="auto" ></video>
        </div>
    )
}

export default VideoPlayer;